<script>
import StyleGuide from '@/components/StyleGuide'
import TestModal from '@/components/TestModal'
export default {
  name: 'HomeView',
  components: {StyleGuide},

  data() {
    return {
      fontWeight: [
        { name: 'light', value: '100', label: 'Light' },
        { name: 'regular', value: '400', label: 'Regular' },
        { name: 'bold', value: '700', label: 'Bold' }
      ],
      pColors: [
        { name: 'red', label: '' },
        { name: 'orange', label: '' },
        { name: 'yellow', label: '' },
        { name: 'lightgreen', label: ' ' },
        { name: 'green', label: '' },
        { name: 'lightblue', label: ' ' },
        { name: 'blue', label: '' },
        { name: 'deepblue', label: ' ' },
        { name: 'deeppurple', label: ' ' },
        { name: 'purple', label: '' },
        { name: 'brown', label: '' },
        { name: 'gray', label: '' }
      ],
      checkboxes: [
        { label: ' 1', value: true },
        { label: ' 2', value: false },
        { label: ' 3', value: false }
      ],
      checkboxes1: [
        { label: ' 4', value: true },
        { label: ' 5', value: false },
        { label: ' 6', value: false }
      ],
      checkboxes2: [
        { label: ' 7', value: true },
        { label: ' 8', value: false },
        { label: ' 9', value: false }
      ],
      buttonType: [
        { type: 'primary', label: '' },
        { type: 'secondary', label: '' },
        { type: 'success', label: '' },
        { type: 'danger', label: '' },
        { type: 'attention', label: '' },
        { type: 'importance', label: '' }
      ],
      tableColumn: [
        { name: 'name', title: '', width: 2, align: 'left' },
        { name: 'age', title: '', width: 2, align: 'center' },
        { name: 'job', title: '', width: 8, align: 'right' }
      ],
      tableValue: [
        { name: '', age: 11, job: '' },
        { name: '', age: 17, job: '' },
        { name: '', age: 31, job: '' }
      ],
      buttonGroup: ['', '', '', '', ''],
      selectedButton: '',
      selectedButton1: 1,
      radio: ['1', '2', '3', '4'],
      radioValue: '1',
      checkboxValue: [],
      switchValue: false,
      switchValueT: 'NO',
      selectedD: new Date().toLocaleDateString(),
      openCal: false,
      direct: 'down',
      position: {},
      cusDate: new Date('2022-12-25'),
      returnData: '',
      //selectbox
      isOpen: false,
      //input
      isEdit: false,
      maxLength: 10,
      counterValue: '',
      errorMsg: '',
      inputValue: 'vue data',
      //pagination
      currentPage: 1,
      currentPage1: 1,
      selectedItem: 1,
      activeTab: 1,
      isCollapse: false,
      themeList: ['', 'light', 'icon', 'icon-bg', 'line'],
      positions: [
        'top-left',
        'top-center',
        'top-right',
        'bottom-left',
        'bottom-center',
        'bottom-right',
        'top-full-width',
        'bottom-full-width'
      ],
      toastSet: {
        round: false,
        useTitle: true,
        useIcon: true,
        closeButton: true,
        theme: '',
        position: 'top-right',
        displayOnTop: true,
        snackbar: false,
        freeze: false,
        timeout: 5000
      },
      toastMsg: {
        title: '',
        description: ''
      }
    }
  },
  computed: {
    checkCounter() {
      return this.maxLength - this.counterValue.length < 0 ? 'danger' : 'success'
    }
  },
  mounted() {
    document.addEventListener('click', () => {
      this.setOpen(false)
    })
  },
  methods: {
    setSelectDate(payload) {
      this.selectedD = payload.toLocaleDateString()
      this.setOpen(false)
    },
    setOpen(value = true) {
      this.setPosition(event)
      // if (!this.$el.contains(event.target)) return
      if (this.openCal) this.openCal = false
      this.openCal = value
      // this.emitter.emit('show-modal', 'test event bus')
    },
    setPosition(e) {
      // console.log(e.target.getBoundingClientRect())
      const { height, top, left } = e.target.getBoundingClientRect()
      const wh = window.innerHeight
      this.direct = 400 < wh - (height + top) ? 'down' : 'up'
      this.position = {
        top: this.direct === 'down' ? `${height + top}px` : `${top - 400}px`,
        left: `${left}px`
      }
    },
    dateToObject(date) {
      return {
        year: date.getFullYear(),
        month: date.getMonth() + 1,
        day: date.getDate(),
        hour: date.getHours(),
        minute: date.getMinutes(),
        second: date.getSeconds()
      }
    },
    returnTestMethod(data) {
      this.returnData = data
      alert(this.returnData)
    },
    setCounter(e) {
      this.counterValue = e.target.value
      if (e.target.value.length > this.maxLength) {
        this.counterValue = this.counterValue.slice(0, -1)
      }
    },
    showModal() {
      const modalProps = {
        title: ' ',
        description: '  .',
        pText: '',
        testFunc: this.returnTestMethod
      }
      this.$gaModal.show({
        comp: TestModal,
        props: modalProps,
        options: {
          clickToClose: false
        }
      })
    },
    onPageChange(page) {
      this.currentPage = page
    },
    onPageChange1(page) {
      this.currentPage1 = page
    },
    closeTag() {
      alert('close tag')
    },
    showToast(type) {
      const contents = {
        title: type.toUpperCase(),
        description: `this is ${type} Toast Test`
      }
      this.$gaToast.show(type, contents, this.toastSet)
    },
    toastStart() {
      const typeIndex = parseInt((Math.random() * 1000) % 12)
      const contents = this.toastMsg
      this.$gaToast.show(this.pColors[typeIndex].name, contents, this.toastSet)
      setTimeout(this.toastStart, 2000)
    }
  }
}
</script>

