<template>
  <div class="modal">
    <div class="modal-header">
      <div class="title">{{ title }} {{ modalId }}</div>
      <div class="close-btn" @click="$emit('close')">
        <i class="fa fa-xmark"></i>
      </div>
    </div>
    <div class="modal-contents">
      <div class="contents">
        <div>{{ description }}</div>
      </div>
    </div>
    <div class="modal-footer align-right">
      <button class="ga-button secondary" @click="addModal"></button>
<!--      <button class="ga-button green" @click="returnTest('')"></button>-->
      <button class="ga-button primary" @click="addConfirm">{{ pText }}</button>
      <button class="ga-button secondary outline" @click="$emit('close')">{{ nText }}</button>
    </div>
  </div>
</template>

<script>
import TestModal from '@/components/TestModal'

export default {
  name: 'TestModal',
  props: {
    modalId: {
      type: Number,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    description: {
      type: String,
      default: ' '
    },
    pText: {
      type: String,
      default: ''
    },
    nText: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      resultConfirm: ''
    }
  },
  watch: {
    resultConfirm() {
      this.returnTest(this.resultConfirm)
    }
  },
  methods: {
    addModal() {
      const modalProps = {
        title: ' ',
        description: '  .',
        pText: ''
      }
      this.$gaModal.show({
        comp: TestModal,
        props: modalProps,
        options: {}
      })
    },
    addConfirm() {
      const modalProps = {
        title: '',
        description: '   ?',
        pText: '',
        result: data => {
          this.returnTest(data)
        }
      }
      this.$gaModal.show({
        comp: 'confirm',
        props: modalProps,
        options: {}
      })
    },
    returnTest(data) {
      this.$attrs.testFunc(data)
    }
  }
}
</script>

<style lang="scss" scoped>
.modal {
  width: 500px;
}
</style>
